.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #0f0;
  border-radius: 50%;
  margin-top: 10%;
  margin-left: 30%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

body {
  max-width: 1400px;
  background: linear-gradient(90deg, #05421c, #000);
  margin: auto;
  display: block;
}

.breed-select {
  max-width: 1000px;
  border: 2px solid #000;
  margin: 30px 30px 0;
  padding: 10px 15px;
  font-style: normal;
  box-shadow: 5px 0 10px rgba(248, 248, 248, .3);
}

.cat-img {
  max-width: 400px;
  max-height: 400px;
  border: 3px solid #000;
  box-shadow: 5px 0 10px rgba(248, 248, 248, .3);
}

.content {
  gap: 30px;
  margin-top: 40px;
  display: flex;
}

.text-content {
  width: 400px;
  color: #000;
  color: #fff;
  background: linear-gradient(90deg, #05421c, #000);
  border: 2px solid #000;
  padding: 30px;
  transition: background .3s;
  box-shadow: inset 5px 5px 10px rgba(248, 248, 248, .3);
}

